@import "../../colors.scss";
@import "../../mixin.scss";

.menu-list-container {
    display: flex;
    flex-flow: column;
    align-items: center;
    width: 200px;
    position: fixed;
    top: 0;
    bottom: 0;
    z-index: 5;
    background-color: $color_tertiary;
    transition: all 0.5s ease-in-out;
    transform: translateX(-200px);
    @include box-shadow(5);

    &.is-open {
        transform: translateX(0);
    }

    .menu-action {
        align-self: flex-end;
        padding: 15px;

    }

    .menu-list {
        width: 100%;

        .menu-item {
            padding: 15px;
            cursor: pointer;

            i {
                margin-right: 10px;
            }

            &:hover {
                &:hover {
                    background-color: rgba($color: #000000, $alpha: 0.08);
                }
            }

        }

    }
}
